import React, { useEffect, useState, useRef } from 'react';

const sliderImages = [
  process.env.PUBLIC_URL + '/images/1000278926.jpg',
  process.env.PUBLIC_URL + '/images/1000278927.jpg',
  process.env.PUBLIC_URL + '/images/1000278928.jpg',
  process.env.PUBLIC_URL + '/images/1000278929.jpg',
];

const ProductList = ({ onAddToCart }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [current, setCurrent] = useState(0);
  const sectionRefs = useRef([]);

  useEffect(() => {
    fetch('products.json')
      .then(res => res.json())
      .then(data => {
        setProducts(data);
        setLoading(false);
      });
  }, []);

  // Detect which product is centered in the viewport
  useEffect(() => {
    const onScroll = () => {
      const sections = sectionRefs.current;
      let minDiff = Infinity;
      let idx = 0;
      sections.forEach((section, i) => {
        if (section) {
          const rect = section.getBoundingClientRect();
          const diff = Math.abs(rect.top + rect.height / 2 - window.innerHeight / 2);
          if (diff < minDiff) {
            minDiff = diff;
            idx = i;
          }
        }
      });
      setCurrent(idx);
    };
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => window.removeEventListener('scroll', onScroll);
  }, [products]);

  if (loading) return <p>Loading products...</p>;

  return (
    <div style={{ position: 'relative', minHeight: '100vh' }}>
      <div
        style={{
          height: '100vh',
          overflowY: 'scroll',
          scrollSnapType: 'y mandatory',
          WebkitOverflowScrolling: 'touch'
        }}
      >
        {products.map((product, idx) => (
          <section
            key={product.id}
            ref={el => (sectionRefs.current[idx] = el)}
            style={{
              height: '100vh',
              width: '100vw',
              scrollSnapAlign: 'start',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              position: 'relative',
              background: '#fff'
            }}
          >
            <img
              src={sliderImages[idx]}
              alt={product.title}
              style={{
                width: '100vw',
                height: '100vh',
                objectFit: 'contain',
                objectPosition: 'center',
                position: 'absolute',
                top: 0,
                left: 0,
                zIndex: 1,
                background: '#000'
              }}
            />
            <div
              style={{
                position: 'relative',
                zIndex: 2,
                background: 'rgba(0,0,0,0.4)',
                color: '#fff',
                padding: '24px 16px',
                borderRadius: 16,
                marginBottom: 100,
                textAlign: 'center',
                width: '100%',
              }}
            >
              <h2 style={{ fontSize: 28, fontWeight: 700 }}>{product.title}</h2>
              <p style={{ fontSize: 22, fontWeight: 600 }}>â‚¹{product.price}</p>
            </div>
          </section>
        ))}
      </div>
      <button
        onClick={() => onAddToCart(products[current])}
        style={{
          position: 'fixed',
          bottom: 32,
          left: '50%',
          transform: 'translateX(-50%)',
          background: 'linear-gradient(90deg, #007bff 60%, #00c6ff 100%)',
          color: '#fff',
          border: 'none',
          borderRadius: 24,
          padding: '18px 60px',
          fontSize: 22,
          fontWeight: 700,
          boxShadow: '0 2px 8px rgba(0,123,255,0.08)',
          cursor: 'pointer',
          zIndex: 1002
        }}
      >
        Add to Cart
      </button>
      <style>{`
        html, body, #root {
          height: 100%;
          margin: 0;
          padding: 0;
        }
        @media (max-width: 600px) {
          section {
            border-radius: 0 !important;
          }
          img {
            object-fit: contain !important;
            object-position: center !important;
            width: 100vw !important;
            height: 100vh !important;
            background: #000 !important;
          }
        }
      `}</style>
    </div>
  );
};

export default ProductList;